import request from '@/utils/request'
// 订单列表
export function findAll(data) {
  return request({
    url: `/order/business/order/findAll`,
    method: 'post',
    data
  })
}
// 订单详情
export function findOrderDetailByOrderId(data) {
  return request({
    url: `/order/business/order/findOrderDetailByOrderId`,
    method: 'post',
    data
  })
}
// 订单发货
export function expressOrder(data) {
  return request({
    url: `/order/business/expressCompany/expressOrder`,
    method: 'post',
    data
  })
}
// 查询订单所有备注
export function findOrderRemark(id) {
  return request({
    url: `/order/orderLog/findOrderRemark?orderId=${id}`,
    method: 'get',
  })
}
// 添加订单备注
export function addOrderRemark(data) {
  return request({
    url: `/order/orderLog/addOrderRemark`,
    method: 'post',
    data
  })
}
// 修改订单地址
export function updateAddress(data) {
  return request({
    url: `/order/business/order/updateAddress`,
    method: 'post',
    data
  })
}
