<template>
  <div class="pageContent">
    <!-- 时间轴 -->
    <div class="timeBox" v-if="form.status != 4">
      <!-- 自提 -->
      <div v-if="form.deliveryType == 1">
        <el-steps :active="form.status + 1" finish-status="success" align-center>
          <el-step title="提交订单" :description="form.createTime"></el-step>
          <el-step title="支付订单" :description="form.payTime"></el-step>
          <el-step title="上门提货" :description="form.shipTime"></el-step>
          <el-step title="已完成" :description="form.finishTime"></el-step>
        </el-steps>
      </div>
      <!-- 物流 -->
      <div v-if="form.deliveryType == 0">
        <el-steps :active="form.status + 1" finish-status="success" align-center>
          <el-step title="提交订单" :description="form.createTime"></el-step>
          <el-step title="支付订单" :description="form.payTime"></el-step>
          <el-step title="已发货" :description="form.shipTime"></el-step>
          <el-step title="已完成" :description="form.finishTime"></el-step>
        </el-steps>
      </div>
    </div>
    <!-- 订单信息 -->
    <el-card shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">订单信息</div>
      <!-- 待付款 - 表头 -->
      <div v-if="form.status == 0">
        <el-card class="orderDetail waitpay" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg"
                :src="require('../../../assets/images/order/waitpay.png')"
                fit="fill"></el-image>
              <div>待支付</div>
            </div>
            <div class="detailLi">订单编号：{{form.orderNo}}</div>
          </div>
          <div class="detailLi">下单时间：{{form.createTime}}</div>
        </el-card>
      </div>
      <!-- 待发货 - 表头 -->
      <div v-if="form.status == 1">
        <el-card class="orderDetail waitpay" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/order/waitsend.png')" fit="fill"></el-image>
              <div>{{form.deliveryType == 0 ? '待发货' : '待取货'}}</div>
            </div>
            <div class="detailLi">订单编号：{{form.orderNo}}</div>
          </div>
          <div class="detailLi">下单时间：{{form.createTime}}</div>
          <div class="detailLi">支付时间：{{form.payTime}}</div>
          <div class="detailLi">交易流水号：{{form.payNumber}}</div>
        </el-card>
      </div>
      <!-- 待收货 - 表头 -->
      <div v-if="form.status == 2">
        <el-card class="orderDetail hadsend" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/order/hadsend.png')" fit="fill"></el-image>
              <div>{{form.deliveryType == 0 ? '已发货' : '已提货'}}</div>
            </div>
            <div class="detailLi">订单编号：{{form.orderNo}}</div>
          </div>
          <div class="detailLi">下单时间：{{form.createTime}}</div>
          <div class="detailLi">支付时间：{{form.payTime}}</div>
          <div class="detailLi">{{form.deliveryType == 0 ? '发货时间：' : '提货时间：'}}{{form.shipTime}}</div>
          <div class="detailLi">交易流水号：{{form.payNumber}}</div>
        </el-card>
      </div>
      <!-- 已完成 - 表头 -->
      <div v-if="form.status == 3">
        <el-card class="orderDetail hadsend" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/order/finish.png')" fit="fill"></el-image>
              <div>交易完成</div>
            </div>
            <div class="detailLi">订单编号：{{form.orderNo}}</div>
          </div>
          <div class="detailLi">下单时间：{{form.createTime}}</div>
          <div class="detailLi">支付时间：{{form.payTime}}</div>
          <div class="detailLi">{{form.deliveryType == 0 ? '发货时间：' : '提货时间：'}}{{form.shipTime}}</div>
          <div class="detailLi">完成时间：{{form.finishTime}}</div>
          <div class="detailLi">交易流水号：{{form.payNumber}}</div>
        </el-card>
      </div>
      <!-- 已关闭 - 表头 -->
      <div v-if="form.status == 4">
        <el-card class="orderDetail close" shadow="always">
          <div class="flex-row-between" style="margin-bottom:20px;">
            <div class="detailTitle flex-row-start">
              <el-image class="detailImg" :src="require('../../../assets/images/order/close.png')" fit="fill"></el-image>
              <div>交易关闭</div>
            </div>
            <div class="detailLi">订单编号：{{form.orderNo}}</div>
          </div>
          <div class="detailLi">下单时间：{{form.createTime}}</div>
          <div class="detailLi">关闭时间：{{form.estimateCloseTime}}</div>
          <div class="detailLi" v-if="form.closeType == 0">关闭原因：订单因超时未付款，系统自动关闭</div>
          <div class="detailLi" v-if="form.closeType == 1">关闭原因：买家已取消订单，系统自动关闭。取消原因：{{form.closeReason}}</div>
          <div class="detailLi" v-if="form.closeType == 2">关闭原因：订单因全部退款，系统自动关闭</div>
        </el-card>
      </div>
    </el-card>
    <!-- 买家留言 -->
    <el-card shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">买家留言</div>
      <div class="fontLi">{{form.userRemark ? form.userRemark : '无留言'}}</div>
    </el-card>
    <!-- 商家备注 -->
    <el-card shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">商家备注</div>
      <div v-if="remarkList.length > 0">
        <div class="flex-row-start remarkLi" v-for="(item,index) in remarkList" :key="index">
          <div class="remarkT">{{item.createTime}}</div>
          <div class="remarkV">{{item.remark}}</div>
        </div>
      </div>
      <div class="fontLi" v-else>暂无备注</div>
      <div style="margin-top:20px;" v-show="!remarkInputShow">
        <el-button type="primary" :disabled="remarkList.length >= 5" @click="remarkInputShow = true">添加备注</el-button>
        <span style="color:#909399;">一个订单最多只可添加5条备注</span>
      </div>
      <div v-if="remarkInputShow" style="margin-top:20px;">
        <div>
          <el-input style="width:400px;" type="textarea" placeholder="请输入备注" v-model.trim="remarkForm.remark" maxlength="100" show-word-limit></el-input>
        </div>
        <div>
          <el-button type="text" style="color:#909399;" @click="remarkInputShow = false">取 消</el-button>
          <el-button type="text" style="margin-left:20px;" :loading="addRemarkLoading" @click="subRemark">确 定</el-button>
        </div>
      </div>
    </el-card>
    <!-- 收货人信息 -->
    <el-card shadow="always" style="margin-bottom:20px;" v-if="form.deliveryType == 0">
      <div class="pageHead">收货人信息</div>
      <el-table :data="[form]" border style="width: 100%">
        <el-table-column label="收货人" prop="receiverName"></el-table-column>
        <el-table-column label="手机号码" prop="receiverPhone"></el-table-column>
        <el-table-column label="收货地址">
          <template slot-scope="scope">
            <div>{{scope.row.receiverProvinceName + scope.row.receiverCityName + scope.row.receiverDistrictName + scope.row.receiverAddress}}</div>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 自提信息 -->
    <el-card shadow="always" style="margin-bottom:20px;" v-if="form.deliveryType == 1">
      <div class="pageHead">自提信息</div>
      <el-table :data="[form]" border style="width: 100%">
        <el-table-column label="自提人" prop="receiverName"></el-table-column>
        <el-table-column label="自提人电话" prop="receiverPhone"></el-table-column>
        <el-table-column label="提货地址">
          <template slot-scope="scope">
            <div>{{scope.row.receiverProvinceName + scope.row.receiverCityName + scope.row.receiverDistrictName + scope.row.receiverAddress}}</div>
          </template>
        </el-table-column>
        <el-table-column label="预约提货时间" prop="selffetchTime"></el-table-column>
      </el-table>
    </el-card>
    <!-- 商品信息 -->
    <el-card shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">商品信息</div>
      <el-table :data="form.list" border style="width: 100%">
        <el-table-column prop="productName" label="商品名称" width="300"></el-table-column>
        <el-table-column label="商品编号" width="180">
          <template slot-scope="scope">
            <div>{{scope.row.productCode ? scope.row.productCode : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column label="规格" width="240">
          <template slot-scope="scope">
            <div>{{scope.row.skuName ? scope.row.skuName : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="productCount" label="商品数量" width="180"></el-table-column>
        <el-table-column prop="originalPrice" label="单价" width="180"></el-table-column>
        <el-table-column label="优惠金额" width="180">
          <template slot-scope="scope">
            <div>{{scope.row.couponDiscountPrice ? scope.row.couponDiscountPrice + '优惠券优惠金额' : ''}}</div>
            <div>{{scope.row.selfDiscountPrice ? scope.row.selfDiscountPrice + '自定义优惠金额' : ''}}</div>
            <div>{{scope.row.promotionDiscountPrice ? scope.row.promotionDiscountPrice + '促销优惠金额，满减，满折' : ''}}</div>
            <div v-if="scope.row.activityDiscountPrice">{{scope.row.activityDiscountPrice}} {{scope.row.activityType == 1 ? '限时折扣' : ''}} {{scope.row.activityType == 2 ? '砍价' : ''}}</div>
            <div>{{scope.row.vipDiscountPrice ? scope.row.vipDiscountPrice + '会员优惠金额' : ''}}</div>
            <div v-if="
              !scope.row.couponDiscountPrice &&
              !scope.row.selfDiscountPrice && 
              !scope.row.promotionDiscountPrice && 
              !scope.row.activityDiscountPrice && 
              !scope.row.vipDiscountPrice
            "
            > - </div>
          </template>
        </el-table-column>
        <el-table-column v-if="form.status == 0" prop="payPrice" label="应付金额" width="180"></el-table-column>
        <el-table-column v-else-if="form.status == 4" prop="payPrice" label="应/实付金额" width="180"></el-table-column>
        <el-table-column v-else prop="payPrice" label="实付金额" width="180"></el-table-column>
        <el-table-column prop="tradePrice" label="" width="180">
          <template slot="header" slot-scope="scope">
            交易服务费（元）
            <el-tooltip :data-type="scope.row" class="item" effect="dark" content="根据单个商品的应/根据单个商品的应/实付金额计算，比例为千分之六，由微信收取，四舍五入，仅供参考，具体金额请以实际流水为准。" placement="top">
              <i class="el-icon-question" ></i>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="退款状态" width="180">
          <template slot-scope="scope">
            <div v-if="scope.row.productStatus == 0">无退款</div>
            <div v-if="scope.row.productStatus == 1">退款中</div>
            <div v-if="scope.row.productStatus == 2">已退款</div>
            <div v-if="scope.row.productStatus == 3">售后处理中</div>
          </template>
        </el-table-column>
      </el-table>
      <div style="float: left;margin: 5px;width: 100%;text-align: right;padding-right: 30px;">
        商品总金额：
        <span class="color-danger">￥{{form.totalAmount}}</span>
      </div>
      <div v-if="form.deliveryType == 0" style="float: left;margin: 5px;width: 100%;text-align: right;padding-right: 30px;">
        +运费：
        <span class="color-danger">￥{{form.freightAmount}}</span>
      </div>
      <div style="float: left;margin: 5px;width: 100%;text-align: right;padding-right: 30px;">
        -优惠金额：
        <span class="color-danger">￥{{form.totalDiscountAmount}}</span>
      </div>
      <div style="float: left;margin: 5px;width: 100%;text-align: right;padding-right: 30px;">
        <span style="border-top:1px solid #303133;padding:5px 0 0 50px">
          <span style="font-weight:700">合计：</span>   
          <span v-if="form.status == 0" style="font-size:17px">应付总金额：</span>
          <span v-else-if="form.status == 4" style="font-size:17px">应/实付总金额</span>
          <span v-else style="font-size:17px">实付总金额：</span>
          <span class="color-danger">￥{{form.payAmount}}</span>
        </span>
      </div>
    </el-card>
    <!-- 付款信息 -->
    <el-card shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">付款信息</div>
      <!-- 待付款 -->
      <div>
        <el-table :data="[form]" border style="width: 100%">
          <el-table-column label="付款方式">
            <template slot-scope="scope">
              <div v-if="scope.row.payType == 0">H5微信代销</div>
              <div v-else-if="scope.row.payType == 1">H5微信自有</div>
              <div v-else-if="scope.row.payType == 2">小程序支付</div>
              <div v-else>-</div>
            </template>
          </el-table-column>
          <el-table-column label="商品总金额" prop="totalAmount"></el-table-column>
          <el-table-column label="优惠金额">
            <template slot-scope="scope">
              <div>{{scope.row.couponDiscountAmount ? scope.row.couponDiscountAmount + '优惠券优惠金额' : ''}}</div>
              <div>{{scope.row.selfDiscountAmount ? scope.row.selfDiscountAmount + '自定义优惠金额' : ''}}</div>
              <div>{{scope.row.promotionDiscountAmount ? scope.row.promotionDiscountAmount + '促销优惠金额，满减，满折' : ''}}</div>
              <div v-if="scope.row.activityDiscountAmount">{{scope.row.activityDiscountAmount}} {{scope.row.activityType == 1 ? '限时折扣' : ''}} {{scope.row.activityType == 2 ? '砍价' : ''}}</div>
              <div>{{scope.row.vipDiscountAmount ? scope.row.vipDiscountAmount + '会员优惠金额' : ''}}</div>
              <div v-if="
                !scope.row.couponDiscountAmount &&
                !scope.row.selfDiscountAmount && 
                !scope.row.promotionDiscountAmount && 
                !scope.row.activityDiscountAmount && 
                !scope.row.vipDiscountAmount
              "
              > - </div>
            </template>
          </el-table-column>
          <el-table-column v-if="form.status == 0" label="应付总金额" prop="payAmount"></el-table-column>
          <el-table-column v-else-if="form.status == 4" label="应/实付总金额" prop="payAmount"></el-table-column>
          <el-table-column v-else label="实付总金额" prop="payAmount"></el-table-column>
          <el-table-column v-if="form.status != 0 && form.status != 4" label="支付流水号" prop="payNumber"></el-table-column>
          <el-table-column v-if="form.status != 4" prop="tradeAmount">
            <template slot="header" slot-scope="scope">
            交易服务费（元）
            <el-tooltip :data-type="scope.row" class="item" effect="dark" content="交易服务费根据订单总金额计算得出，比例为千分之六，由微信收取（含商品金额以及运费金额）。仅供参考，具体金额请以实际流水为准。" placement="top">
              <i class="el-icon-question" ></i>
            </el-tooltip>
          </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
    <!-- 配送信息 -->
    <el-card shadow="always" style="margin-bottom:20px;" v-if="form.deliveryType == 0">
      <div class="pageHead">配送信息</div>
      <el-table :data="[form]" border style="width: 100%">
        <el-table-column label="配送方式">
          <template slot-scope="scope">
            <div>{{scope.row.expressName ? scope.row.expressName : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column label="运费">
          <template slot-scope="scope">
            <div>{{scope.row.freightAmount ? scope.row.freightAmount : '-'}}</div>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <div class="flex-row-end">
      <el-button v-if="(form.deliveryType == 0 && form.status == 2) || (form.deliveryType == 0 && form.status == 3)" @click="showpost" type="primary">追踪物流</el-button>
      <el-button type="primary" @click="goBack">返回上一页</el-button>
    </div>
    <!-- 追踪物流 -->
    <postTree :postShow="postShow" :postNum="form.expressNo" @closeTree="closeTree"></postTree>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findOrderDetailByOrderId , findOrderRemark , addOrderRemark } from '@/api/order/orderList'
import postTree from '@/components/postTree/postTree';

export default {
  data(){
    return{
      form:{},                  // 详情数据
      remarkList:[],            // 备注
      remarkInputShow:false,    // 添加备注
      remarkForm:{},            // 添加备注数据
      addRemarkLoading:false,   // 添加备注loading
      order:{},
      postShow:false,
      postNum:''
    }
  },
  created(){
    this.getData()
    this.getRemarkList()
  },
  methods:{
    showpost(){
      this.postNum = this.form.expressNo
      this.postShow = true;
    },
    closeTree(){
      this.postShow = false;
    },
    getData(){
      findOrderDetailByOrderId({orderId:this.$route.query.orderId}).then(res=>{
        console.log('订单详情',res)
        if(res.code == 200){
          this.form = res.data
        }
      })
    },
    // 获取备注列表
    getRemarkList(){
      findOrderRemark(this.$route.query.orderId).then(res => {
        console.log('备注列表',res)
        if(res.code == 200){
          this.remarkList = res.data;
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 备注弹出框 - 添加备注
    subRemark(){
      console.log('orderRemark',this.remarkForm.remark)
      if(!this.remarkForm.remark){
        this.$message.error('请输入备注内容');
        return;
      }
      this.remarkForm.orderId = this.$route.query.orderId
      this.remarkForm.remarkType = 0
      this.remarkForm.createUser = this.user.userId
      console.log('添加备注请求的参数',this.remarkForm)
      this.addRemarkLoading = true;
      addOrderRemark(this.remarkForm).then(res=>{
        console.log('添加备注',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getRemarkList(this.remarkForm.orderId)
          this.remarkForm.remark = '';
          this.remarkInputShow = false;
          setTimeout(()=>{
            this.addRemarkLoading = false;
          },1000)
        }else{
          this.addRemarkLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 返回上一页
    goBack(){
      this.$router.go(-1)
    },
  },
  components:{
    postTree,
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .timeBox{
    padding: 0 20px 20px;
  }
  .orderDetail{
    border-color: rgba(255, 188, 59,0);
    &.waitpay{
      background-color: rgba(255, 188, 59,0.3);
      .detailTitle{
        color: #FFBC3B;
      }
    }
    &.hadsend{
      background-color: rgba(63, 203, 31, 0.3);
      .detailTitle{
        color: #3FCB1F;
      }
    }
    &.close{
      background-color: rgba(43, 139, 255, 0.3);
      .detailTitle{
        color: #2B8BFF;
      }
    }
    .detailTitle{
      line-height: 40px;
      font-weight: bold;
      font-size: 18px;
      .detailImg{
        width: 26px;
        height: 26px;
        margin-right: 10px;
      }
    }
    .detailLi{
      color: #333333;
      line-height: 40px;
    }
    .fontLi{
      width: 100%;
      line-height: 24px;
      font-size: 14px;
      color: #333333;
    }
  }
  .remarkLi{
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 20px;
    .remarkT{
      color: #909399;
    }
    .remarkV{
      margin-left: 20px;
      color: #303133;
    }
  }
</style>