<template>
  <el-dialog title="物流跟踪" :visible.sync="postShow" width="30%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <div class="block">
      <div v-if="treeList.length > 0">
        <el-timeline v-for="(item,index) in treeList" :key="index">
          <el-timeline-item :timestamp="item.ftime" placement="top">
            <el-card>
              <p>{{item.context}}</p>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </div>
      <div v-else>{{message}}</div>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="closeTree">关 闭</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { getlogisticsRealtimeInfo} from '@/api/logistics/post'
export default {
  props:['postNum','postShow'],
  watch:{
    postNum(newvalue,oldValue){
      if(newvalue){
        this.getData()
      }
    }
  },
  data(){
    return{
      treeList:[],
      message:''
    }
  },
  created(){},
  methods:{
    getData(){
      getlogisticsRealtimeInfo(this.postNum).then(res=>{
        console.log('物流树',res)
        if(res.data != null) this.treeList = res.data.data
        this.message = res.message
      })
    },
    closeTree(){
      this.$emit('closeTree',true)
    }
  }
}
</script>